/* src/styles/styles.css */

.typeahead-form {
  position: relative;
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  display: none;
}

.typeahead-form.loaded {
  display: block;
}

.typeahead-list {
  position: absolute;
  width: 100%;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 4px;
  max-height: 200px;
  overflow-y: auto;
  z-index: 1000;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.typeahead-item {
  display: flex;
  padding: 8px 0 8px 8px;
  cursor: pointer;
  text-decoration: none;
  position: relative;
}

.identifier {
  flex: 0 0 20px;
  color: gray;
  font-size: 0.9em;
  margin-right: 12px;
  font-family: monospace;
  text-align: right;
  min-width: 30px;
}

.identifier .highlight {
  padding: 4px 2px 1px 2px;
  margin: 0 -2px;
}

.typeahead-item.selected {
  background-color: #e0e0e0;
}

.typeahead-item[data-resource="true"]::before {
  content: "";
  width: 4px;
  height: 2rem;
  background-color: yellow;
  position: absolute;
  left: 0px;
  top: 8px;
  border-radius: 0 4px 4px 0;
}

.typeahead-item[data-resource="true"] .identifier {
  font-weight: bold;
}

.typeahead-item[data-resource="true"] .identifier .highlight {
  padding: 1px 2px;
}

.typeahead-item:hover
{
  background-color: #eeeeee;
}

.label {
  white-space: normal;
  display: inline-block;
}

.highlight {
  font-weight: bold;
  background-color: yellow;
  border-radius: 2px;
}

span.label, span.identifier {
  line-height: 2rem;
}
